.col-sm-6 .paragraph__column--container-wrapper .container,
.col-sm-9 .paragraph__column--container-wrapper .container {
  width: 100%;
}

.paragraph {
    &.paragraph--width--tiny,
    &.paragraph--width--narrow,
    &.paragraph--width--medium,
    &.paragraph--width--wide,
    &.paragraph--width--full {
      .paragraph__column--container-wrapper {
        .container {
          width: 100%;
        }
      }
    }
  > .paragraph__column {
    .row__title {
      text-align: center;
      padding-left: 15px;
      padding-right:15px;
    }
    >.container {
      padding-left:0;
      padding-right:0;
      >.paragraph__title {
        border-radius: $border-radius;
      }
    }
  }
    &.paragraph--color {
      > .paragraph__column {
        padding-top:0;
        padding-bottom:0;
      }
    }

  .paragraph__column--container-wrapper {
    padding-top: 30px;
    padding-bottom:30px;
      @media (max-width: $screen-sm-min) {
        display: block;
        padding-bottom:15px;
      }
      .container {
        display: flex;
        flex-wrap: no-wrap;
          @media (max-width: $screen-sm-min) {
            display: block;
        }
      }
      [class^="paragraph--type--bp-columns__"] {
        display: flex;
        @media (max-width: $screen-xs-max) {
          margin-bottom:15px;
          &:first-child {
            padding-left:0;
            padding-right:0;
          }
          &:last-child {
            padding-left:0;
            padding-right:0;
            margin-bottom:0px;
          }
        }
        &:first-child {
          padding-left:0;
        }
        &:last-child {
          padding-right:0;
        }
      }
      .paragraph {
        &.paragraph--color {
          display: block;
          border-radius: $border-radius;
        }
        &.paragraph__card {
          > .paragraph__column {
            padding-top: 0;
            > .paragraph__title {
              float: left;
              width:100%;
              background-color: $color-pacific-blue;
              border-radius: $border-radius;
            }
            .field--name-field-card-title {
              color: $color-white;
              font-size: floor(($font-size-base * 1.3));
              font-weight:$font-weight-500;
              padding:8px 12px;
            }
            .field--name-field-card-body {
              float: left;
              padding: 9px;
              width: 100%;
              > h1,h2,h3,h4,h5,h6 {
                &:first-child {
                  margin-top:10px;
                }
              }
            }
            .field--name-field-card-background-image {
              //line-height:0; // body line height override
              [class^="hero__responsive_"] {
                -webkit-background-size:cover;
                background-size:cover;
                min-height: 1px;
              }
            }
          }
        }
        &.paragraph--type--card-basic {
          &.paragraph--width--tiny,
          &.paragraph--width--narrow,
          &.paragraph--width--medium,
          &.paragraph--width--wide,
           &.paragraph--width--full {
            padding-right:0;
            padding-left:0;
          }
          &.paragraph--width--tiny.paragraph--alignment.paragraph--alignment-right {
            @media (min-width: $screen-sm) {
              margin-left:66.66666667%;
            }
          }
          &.paragraph--width--tiny.paragraph--alignment.paragraph--alignment-left,
          &.paragraph--width--narrow.paragraph--alignment.paragraph--alignment-left,
          &.paragraph--width--medium.paragraph--alignment.paragraph--alignment-left,
          &.paragraph--width--wide.paragraph--alignment.paragraph--alignment-left {
            @media (min-width: $screen-sm) {
              margin-left:0;
            }
          }
          &.paragraph--width--narrow.paragraph--alignment.paragraph--alignment-right {
            @media (min-width: $screen-sm) {
              margin-left:50%;
            }
          }
          &.paragraph--width--medium.paragraph--alignment.paragraph--alignment-right {
            @media (min-width: $screen-sm) {
              margin-left: 33.33333333%;
            }
          }
          &.paragraph--width--wide.paragraph--alignment.paragraph--alignment-right {
            @media (min-width: $screen-sm) {
              margin-left: 16.66666667%;
            }
          }
        }
        // Card Content Alignment field settings
        &.paragraph--alignment-top {
          align-self: flex-start;
        }
        &.paragraph--alignment-bottom {
          align-self: flex-end;
        }
      }
    }
  //}
}
.paragraph.paragraph--color + .paragraph > .paragraph__column {
  padding-top: 0;
}
