/* HEADER (global page header)
 1. Header (logo)
 2. Google Translation
 3. Search
 */

// [1]
.header {
  background-color: #fff;
  padding:12px 0 6px 0;
  width: 100%;
  &__logo {
      display: block;
    &--image {
      @media (max-width: $screen-xs-min) {
        height: 30px;
      }
      height:40px;
    }
  }
  // [2]
  &__translation {
    display: inline-block;
    line-height: 2.4;
    .goog-te-gadget-simple {
      background-color: transparent;
      border:0;

      display: inline-block;
      font-family: $font-family-sans-serif;
      font-size: ceil(($font-size-base * 1.05));
      @media (min-width: $screen-xs-max) {
        position: relative;
        //right: 112px;
        right: 105px;
        z-index: 200;
      }
      @media (min-width: $screen-md) {
        right: 120px;
      }
      @media (min-width: $screen-md-lg) {
        position: static;
      }
    }
    // Google brand icon
    .goog-te-gadget-icon {
      display: none;
    }
    // Google pipe and down arrow
    .goog-te-menu-value {
      color: $color-brand-slate!important;
      span:nth-of-type(n+2) {
        display: none;
      }
    }
  }
  .col-sm-4 {
    @media (max-width: $screen-xs-max) {
      display: flex;
      align-items: center;
      justify-content: center;
      position: static;
    }
    @media (min-width: $screen-sm-min) {
      text-align: right;
    }
  }
  // [3]
  &__search {
    display: inline-block;
    &--icon {
        &:before {
          font-family: FontAwesome;
          content: "\f002";
          color: $color-brand-green;
          font-size: $font-size-base * 1.5;
        }
        &:hover:before {
          content: "\f00e";
        }
      &.search--open {
        &:before {
          content: "\f010";
        }
      }
    }
    @media (max-width: $screen-xs-max) {
      position: absolute;
      top: 16px;
      right: 105px;
    }
    @media (min-width: $screen-xs-max) {
      position: absolute;
      top: 8px;
      right: 95px;
      z-index: 200;
    }
    @media (min-width: $screen-md) {
      right: 105px;
    }
    @media (min-width: $screen-md-lg) {
      position: static;
    }
  }
}
